'use strict';

module.exports = validate;

var set = require('lodash/set');

var get = require('lodash/get');

var HttpError = require('../../request/http-error');

var deprecate = require('../../deprecate');

function validate(endpointParams, options) {
  // Alias are handled before validation, as validation rules
  // ar set the aliased parameter. The `mapTo` property is the other way
  // around, the final parameter name is the mapTo value, but validation
  // rules are on parameter with the mapTo property
  Object.keys(options).forEach(function (optionName) {
    if (!endpointParams[optionName] || !endpointParams[optionName].alias) {
      return;
    }

    set(options, endpointParams[optionName].alias, options[optionName]);
    delete options[optionName]; // right now all parameters with an alias property also have a deprecated
    // property, but that might change in future, so we wrap it in the if block,
    // but ignore if for coverage

    /* istanbul ignore else */

    if (endpointParams[optionName].deprecated) {
      deprecate("\"".concat(optionName, "\" parameter has been renamed to \"").concat(endpointParams[optionName].alias, "\""));
    }
  });
  Object.keys(endpointParams).forEach(function (parameterName) {
    var parameter = get(endpointParams, parameterName);
    var expectedType = parameter.type;
    var parentParameterName;
    var parentValue;
    var parentParamIsPresent = true;
    var parentParameterIsArray = false;

    if (/\./.test(parameterName)) {
      parentParameterName = parameterName.replace(/\.[^.]+$/, '');
      parentParameterIsArray = parentParameterName.slice(-2) === '[]';

      if (parentParameterIsArray) {
        parentParameterName = parentParameterName.slice(0, -2);
      }

      parentValue = get(options, parentParameterName);
      parentParamIsPresent = parentParameterName === 'headers' || typeof parentValue === 'object' && parentValue !== null;
    }

    var values = parentParameterIsArray ? (get(options, parentParameterName) || []).map(function (value) {
      return value[parameterName.split(/\./).pop()];
    }) : [get(options, parameterName)];
    values.forEach(function (value, i) {
      var valueIsPresent = typeof value !== 'undefined';
      var valueIsNull = value === null;
      var currentParameterName = parentParameterIsArray ? parameterName.replace(/\[\]/, "[".concat(i, "]")) : parameterName;

      if (!parameter.required && !valueIsPresent) {
        return;
      } // if the parent parameter is of type object but allows null
      // then the child parameters can be ignored


      if (!parentParamIsPresent) {
        return;
      }

      if (parameter.allowNull && valueIsNull) {
        return;
      }

      if (!parameter.allowNull && valueIsNull) {
        throw new HttpError("'".concat(currentParameterName, "' cannot be null"), 400);
      }

      if (parameter.required && !valueIsPresent) {
        throw new HttpError("Empty value for parameter '".concat(currentParameterName, "': ").concat(JSON.stringify(value)), 400);
      } // parse to integer before checking for enum
      // so that string "1" will match enum with number 1


      if (expectedType === 'integer') {
        var unparsedValue = value;
        value = parseInt(value, 10);

        if (isNaN(value)) {
          throw new HttpError("Invalid value for parameter '".concat(currentParameterName, "': ").concat(JSON.stringify(unparsedValue), " is NaN"), 400);
        }
      }

      if (parameter.enum && parameter.enum.indexOf(value) === -1) {
        throw new HttpError("Invalid value for parameter '".concat(currentParameterName, "': ").concat(JSON.stringify(value)), 400);
      }

      if (parameter.validation) {
        var regex = new RegExp(parameter.validation);

        if (!regex.test(value)) {
          throw new HttpError("Invalid value for parameter '".concat(currentParameterName, "': ").concat(JSON.stringify(value)), 400);
        }
      }

      if (expectedType === 'object' && typeof value === 'string') {
        try {
          value = JSON.parse(value);
        } catch (exception) {
          throw new HttpError("JSON parse error of value for parameter '".concat(currentParameterName, "': ").concat(JSON.stringify(value)), 400);
        }
      }

      set(options, parameter.mapTo || currentParameterName, value);
    });
  });
  return options;
}