module.exports = apiMethod;

var clone = require('lodash/clone');

var defaultsDeep = require('lodash/defaultsDeep');

var mapKeys = require('lodash/mapKeys');

var deprecate = require('../../deprecate');

var validate = require('./validate');

function apiMethod(octokit, endpointDefaults, endpointParams, options, callback) {
  // Do not alter passed options (#786)
  options = clone(options) || {}; // lowercase header names (#760)

  options.headers = mapKeys(options.headers, function (value, key) {
    return key.toLowerCase();
  });

  if (endpointDefaults.deprecated) {
    deprecate(endpointDefaults.deprecated);
    delete endpointDefaults.deprecated;
  }

  var endpointOptions = defaultsDeep(options, endpointDefaults);
  var promise = Promise.resolve(endpointOptions).then(validate.bind(null, endpointParams)).then(octokit.request);

  if (callback) {
    deprecate('callbacks will be removed in v16. Use async/await or Promises instead.');
    promise.then(callback.bind(null, null), callback);
    return;
  }

  return promise;
}