'use strict';

module.exports = restEndpoint;

var defaultsDeep = require('lodash/defaultsDeep');

var intersection = require('lodash/intersection');

var mapKeys = require('lodash/mapKeys');

var omit = require('lodash/omit');

var urlTemplate = require('url-template');

var getUserAgent = require('universal-user-agent');

var addQueryParameters = require('./add-query-parameters');

var deprecate = require('../deprecate');

var extractUrlVariableNames = require('./extract-url-variable-names');

var pkg = require('../../package.json');

var DEFAULTS = module.exports.DEFAULTS = require('./defaults');

var NON_PARAMETERS = ['request', 'baseUrl'];

function restEndpoint(options) {
  // lowercase header names (#760)
  options.headers = mapKeys(options.headers, function (value, key) {
    return key.toLowerCase();
  });
  var userAgent = "octokit.js/".concat(pkg.version, " ").concat(getUserAgent());

  if (options.headers['user-agent']) {
    userAgent = "".concat(options.headers['user-agent'], " ").concat(userAgent);
  }

  options.headers['user-agent'] = userAgent;
  options = defaultsDeep({}, options, DEFAULTS);
  var method = options.method.toLowerCase();
  var baseUrl = options.baseUrl;
  var url = options.url;
  var body = options.body;
  var headers = options.headers;
  var remainingOptions = omit(options, ['method', 'baseUrl', 'url', 'headers']); // replace :varname with {varname} to make it RFC 6570 compatible

  url = url.replace(/:([a-z]\w+)/g, '{+$1}'); // extract variable names from URL to calculate remaining variables later

  var urlVariableNames = extractUrlVariableNames(url);
  url = urlTemplate.parse(url).expand(remainingOptions);

  if (!/^http/.test(url)) {
    url = baseUrl + url;
  }

  var requestOptions = remainingOptions.request;
  remainingOptions = omit(remainingOptions, intersection(Object.keys(options), urlVariableNames).concat(NON_PARAMETERS));

  if (method === 'get' || method === 'head') {
    url = addQueryParameters(url, remainingOptions);
  } else {
    if ('input' in remainingOptions) {
      deprecate('"input" option has been renamed to "data"');
      remainingOptions.data = remainingOptions.input;
      delete remainingOptions.input;
    }

    if ('data' in remainingOptions) {
      body = remainingOptions.data;
    } else {
      body = Object.keys(remainingOptions).length ? remainingOptions : undefined;
    }
  }

  return Object.assign(requestOptions, {
    method: method,
    url: url,
    headers: headers,
    body: body
  });
}